 <!--
 * @Description: 意见反馈
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-30 12:52:55
 * @LastEditTime: 2019-10-08 14:05:23
 -->
<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items" />
    <!-- <v-container> -->
    <v-layout row wrap>

      <!-- <v-card-title> -->
        <v-layout row wrap style="margin-left: 40px;" >
          <v-flex xs6 sm4 md2>
            <v-select
              @change="info"
              menu-props="auto"
              v-model="agent_id"
              :items="agent_list"
              item-text="text"
              item-value="value"
              label="所属代理商"
              required
            ></v-select>
          </v-flex>
          <!-- <v-btn color="primary" @click="info(  )">筛选用户</v-btn> -->
        </v-layout>
      <!-- </v-card-title> -->
      <v-flex xs12 mb-12>
        <v-expansion-panel popout dark>
          <v-expansion-panel-content v-for="(item,index) in data" :key="index">
            <template v-slot:header>
              <div v-if="item.status == -1">
                <v-chip color="orange" text-color="white">
                  待处理
                  <v-icon right>wb_incandescent</v-icon>
                </v-chip>
                <v-chip color="orange">{{ item.create_time | time }}</v-chip>
                <v-chip>{{ item.content | slice }}</v-chip>
              </div>
              <div v-else>
                <v-chip color="success" text-color="white">
                  已处理
                  <v-icon right>check_circle</v-icon>
                </v-chip>
                <v-chip color="success">{{ item.create_time | time }}</v-chip>
                <v-chip>{{ item.content | slice }}</v-chip>
              </div>
            </template>
            <v-card>
              <v-card-text>
                <div class="text-xs-left">
                  <v-chip color="secondary" text-color="white">用户预留联系方式</v-chip>
                  <v-chip>{{ item.contact }}</v-chip>
                </div>
                <div class="text-xs-left">
                  <v-chip color="secondary" text-color="white">用户预留反馈意见</v-chip>
                  <!-- <v-chip>{{ item.content }}</v-chip> -->
                  <p style="word-wrap: break-word; word-break: normal;padding: 0 14px 0 40px;color: #FFFF00" > {{ item.content }} </p>
                  <!-- {{ item.content }} -->
                </div>
                <div class="text-xs-left">
                  <v-chip color="secondary" text-color="white">用户反馈意见时间</v-chip>
                  <v-chip>{{ item.create_time | time }}</v-chip>
                </div>
                <div class="text-xs-left">
                  <v-chip color="secondary" text-color="white" style="width: 128px;">用户ID</v-chip>
                  <v-chip>{{ item.user_id }}</v-chip>
                  <span style="font-size: 12px;color: #ccc;">* 用户ID</span>
                </div>
                <div class="text-xs-left" v-if="item.status == 1">
                  <v-chip color="success" text-color="white" style="width: 128px;">已处理描述</v-chip>
                  <v-chip>{{ item.description }}</v-chip>
                </div>
                <template v-for="(item1,index) in root_path">
                  <div
                    class="text-xs-right"
                    :key="index"
                    v-if="item1.url == '/api/v1/user/update_feedback' && item.status == -1"
                  >
                    <v-btn color="warning" @click="ignore_( item.id )">忽略 {{ item.url }}</v-btn>
                    <v-btn color="primary darken-1 white--text" @click="opinion_( item.id )">已处理</v-btn>
                  </div>
                </template>
              </v-card-text>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-flex>
      <!-- 分页组件 -->
      <div class="text-xs-center" style="width: 100%;margin: 20px;" >
        <Page @opinionPage="info_" :total_number="total_number" :page="page+1" />
        <!-- <v-pagination v-model="page" :length="60" :dark="true" :total-visible="7" @input="info_"></v-pagination> -->
      </div>
    </v-layout>
    <!-- </v-container> -->

    <v-alert
      v-if="data.length == 0"
      :value="true"
      color="warning"
      icon="check_circle"
      outline
    >暂无用户反馈意见...</v-alert>
    <!-- 处理描述 -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline"> &nbsp; &nbsp; &nbsp; 用户意见反馈处理描述</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <!-- <v-text-field label="输入描述" required></v-text-field> -->
                  <v-textarea
                    v-model="description" 
                    outline
                    name="input-7-4"
                    label="输入描述"
                    value=""
                  ></v-textarea>
                </v-flex>
              </v-layout>
            </v-container>
            <small> &nbsp; &nbsp; &nbsp; * 已处理用户意见反馈</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary darken-1" flat @click="dialog = false">取消</v-btn>
            <v-btn color="primary darken-1 white--text" @click="opinion">提交</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
    <!-- 确认 -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog_" persistent max-width="290">
        <v-card>
          <v-card-title class="headline">确定要忽略该条意见反馈吗?</v-card-title>
          <v-card-text>确认操作该条意见反馈会被标记为已处理状态。</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary darken-1" flat @click="dialog_ = false">取消</v-btn>
            <v-btn color="primary darken-1 white--text" @click="ignore">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>
<script>
import Breadcrumbs from "../../components/breadcrumbs/breadcrumbs.vue";
import Page from './page'
export default {
  components: { Breadcrumbs, Page },
  data() {
    return {
      status: 0,
      page: 0,
      total_number:0,
      agent_list: [
        { text: "全部APP反馈信息",value: 0},
        { text: "已处理", value: 1},
        { text: "未处理", value: -1}
      ],
      agent_id:{ text: "全部APP反馈信息",value: 0 },
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "用户反馈",
          disabled: false,
          to: "/opinion"
        }
      ],
      data: [],
      description: "",
      dialog: false,
      dialog_: false,
      root_path: this.$cookie.getLocalStorage("root_path").root_path
    };
  },
  mounted() {
    this.info(undefined);
  },
  methods: {
    // 分页触发
    info_( e ){
      this.page = e-1;
      this.info(undefined);
    },
    // 获取列表
    info(event) {
      // 选择框触发
      if( event !== undefined ){
        this.page = 0;
        this.status = event;
      }
      this.$axios({
        method: "get",
        url: `${this.$apis.URL.user_feedback}?status=${this.status}&page=${this.page}`,
        data: {}
      })
        .then(response => {
          if (response.data.result === 0) {
            this.data = response.data.datas.list;
            this.total_number = response.data.datas.total_number;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    // 已处理确认
    opinion_(id) {
      this.dialog = true;
      this.id = id;
    },
    // 提交按钮
    opinion() {
      if (this.description.length < 2) {
        this.$Dialog.getProductCheckResult_({text: "请输入至少2个字符",timeout: 3000, snackbar: true });
      } else {
        this.$axios({
          method: "post",
          url: this.$apis.URL.user_update_feedback,
          data: {
            id: this.id,
            status: 1,
            description: this.description
          }
        })
          .then(response => {
            if (response.data.result === 0) {
              this.dialog = false;
              this.id = "";
              this.info(undefined);
              this.$Dialog.getProductCheckResult_({text: "此条意见反馈已标记为已处理！",timeout: 3000, snackbar: true });
            } else {
              this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
            }
          })
          .catch(error => {
            this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
          });
      }
    },
    // 忽略确认
    ignore_(id) {
      this.dialog_ = true;
      this.id = id;
    },
    // 提交忽略
    ignore() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.user_update_feedback,
        data: {
          id: this.id,
          status: 1,
          description: "忽略"
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.info(undefined);
            this.id = "";
            this.dialog_ = false;
            this.$Dialog.getProductCheckResult_({text: "已忽略该条用户反馈！",timeout: 3000, snackbar: true });
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>

<style lang="less" scoped>
.v-card__text {
  padding: 23px;
  width: 100%;
}
.text-xs-center>div{
text-align: right;
  margin-right: 30px;
}
</style>
